.ServicePackage {
    border: 1px solid lightgrey;
    border-width: 1px 0 1px 0;
    padding: 1em !important;
    .PriceCard {
        text-align: center;
        padding: 0 !important;
        p {
            align-items: center;
            text-align: center;
            display: unset !important;
        }
        .price {
            color: var(--green);
            font-size: 1.2em;
        }
        a {
            width: fit-content;
            align-self: center;
            padding: .15em 1.5em !important;
            background: #000;
            margin: .5em !important;
            font-size: 900;
        }
        &:hover {
            border-color: var(--green);
        }
    }
    .thisPackage {
        .PriceCard {
            background: var(--bg-grey);
            a {
                cursor: default;
                background: transparent;
                color: var(--yellow)
            }
        }
    }
}